.handleForm {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  background-color: black;
  .background {
    width: 100%;
    height: 100%;
  }
  .overlay {
    position: absolute;
    top: 0px;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .container {
      .image {
        width: 100%;
        display: flex;
        background-color: transparent;
        align-items: center;
        justify-content: center;
        margin-bottom: 16px;
        img {
          padding: 5px;
          // border-radius: 50%;
          box-shadow: 0 10px 20px #89caff94;
          height: 85px;
          width: 85px;
        }
      }
      background-color: black;
      width: 750px;
      height: 450px;
      position: relative;
      display: grid;
      grid-template-columns: 1fr 1fr;
      place-items: center;
      border-radius: 5px;
      line-height: 1.5;
      box-shadow: 0 20px 30px rgba(214, 198, 198, 0.185);

      &::after {
        content: "";
        position: absolute;
        background-image: linear-gradient(to bottom right, #ff9ec4, #ffd07e);
        width: 900px;
        height: 900px;
        border-radius: 50%;
        z-index: -1;
      }

      &-close {
        position: absolute;
        top: -15px;
        right: -15px;
        background-color: rgb(32, 32, 72);
        width: 45px;
        height: 45px;
        display: grid;
        place-items: center;
        font-size: 1.7rem;
        color: #2e4052;
        border-radius: 50%;
        box-shadow: 0 10px 20px #89caff94;
        cursor: pointer;
      }

      img {
        width: 100%;
        height: 400px;
        object-fit: cover;
        object-position: center;
      }

      &-text {
        padding: 10px 40px 10px 10px;
        .image {
          width: 100%;
          display: flex;
          background-color: transparent;
          align-items: center;
          justify-content: center;
          margin-bottom: 16px;
          img {
            padding: 5px;
            // border-radius: 50%;
            box-shadow: 0 10px 20px #89caff94;
            height: 85px;
            width: 85px;
          }
        }
        h2 {
          color: white;
          font-size: 20px;
        }

        p {
          font-size: 14px;
          color: white;
          margin: 10px 0;
        }

        input,
        button {
          width: 100%;
          border: none;
          padding: 14px;
          border-radius: 3px;
        }


        input {
          border: 2px solid #daddec;
          margin: 5px 0 10px;
          font-size: 1rem;
          color: #656880;
        }
        input {
          border: 2px solid #daddec;
          margin: 5px 0 10px;
          font-size: 1rem;
          color: #656880;
        }

        button {
          background-image: linear-gradient(to right, #3b3c3d, #343842);
          display: block;
          color: wheat;
          font-size: 1rem;
          cursor: pointer;
          box-shadow: 0 5px 20px #89caff94;
          transition: box-shadow 0.3s ease-in-out;
        }

        button:hover {
          box-shadow: none;
        }

        span {
          display: block;
          text-align: center;
          margin: 20px 0 0;
          color: #babdcb;
          font-size: 12px;
        }
      }
    }
  }
}
